import axios from "axios"
import router from "vue-router"

export default {
    namespaced: true,
    state: {
        products: [],
    },
    getters: {
        get_products(state) {
            return state.products
        },
    },
    mutations: {
        SET_PRODUCTs(state, products) {
            state.products = products
        },
        ADD_PRODUCT(state, product) {
            state.products.push(product)
        },
        DELETE_PRODUCT(state, productId) {
            state.products.map((item, i) => {
                if (item.id == productId) {
                    state.products.splice(i, 1)
                }
            })
        },
        EDIT_PRODUCT(state, product) {
            state.products.map((item) => {
                if (item.id == product.id) {
                    item.title = product.title
                    item.description = product.description
                    item.price = product.price
                    item.image = product.image
                }
            })
        },
    },
    actions: {
        async getProducts({ dispatch }) {
            return await axios
                .get("product")
                .then((response) => {
                    dispatch("attempt", response.data)
                })
                .catch((err) => {
                    console.log("error" + err)
                    
                })
        },
        async attempt({ commit }, data) {
            commit("SET_PRODUCTs", data)
        },
        async addProduct({ commit }, payload) {
            let response = await axios.post("product", payload)
            console.log(response.data)
            return commit("ADD_PRODUCT", response.data)
        },
        async deleteProduct({ commit }, productId) {
            let response = await axios.delete(`product/${productId}`)
            commit("DELETE_PRODUCT", productId)
        },
        async editProduct({ commit }, payload) {
            let response = await axios.patch(`product/${payload.id}`, payload)
            console.log(response.data)
            commit("EDIT_PRODUCT", response.data)
        },
    },
}
