<template>
    <b-container>
        <b-row>
            <b-col>
                <h1>Signin</h1>
            </b-col>
        </b-row>
        <b-row>
            <b-col cols="4">
                <b-form @submit.prevent="submit">
                    <b-form-input
                        v-model="form.email"
                        type="email"
                        placeholder="Email"
                        class="mb-2"
                    ></b-form-input>
                    <b-form-input
                        v-model="form.password"
                        type="password"
                        placeholder="Password"
                        class="mb-2 "
                    ></b-form-input>
                    <b-button type="submit" variant="primary">Login</b-button>
                    <router-link to="/signup" class="mx-3"
                        >Register</router-link
                    >
                </b-form>
            </b-col>
        </b-row>
    </b-container>
    <!--  -->
</template>

<script>
import { mapActions } from "vuex"

export default {
    name: "signin",
    components: {},
    data() {
        return {
            form: {
                email: "",
                password: "",
            },
        }
    },
    methods: {
        ...mapActions("auth", ["signin"]),
        submit() {
            this.signin(this.form)
                .then(() => {
                    this.$router.replace("dashboard")
                })
                .catch(() => {
                    console.log("faild")
                })
        },
    },
}
</script>
