<template>
    <b-container>
        <b-row>
            <b-col><h1>Signup</h1></b-col>
        </b-row>
        <b-row>
            <b-col cols="4">
                <b-form @submit.prevent="submit">
                    <b-form-input
                        v-model="form.name"
                        type="text"
                        placeholder="Name"
                        class="mb-2"
                    ></b-form-input>
                    <b-form-input
                        v-model="form.email"
                        type="email"
                        placeholder="Email"
                        class="mb-2"
                    ></b-form-input>
                    <b-form-input
                        v-model="form.password"
                        type="password"
                        placeholder="Password"
                        class="mb-2 "
                    ></b-form-input>
                    <b-button type="submit" variant="primary"
                        >register</b-button
                    >
                    <router-link to="signin" class="mx-3">Login</router-link>
                </b-form>
            </b-col>
        </b-row>
    </b-container>
    <!--  -->
</template>

<script>
import { mapActions } from "vuex"

export default {
    name: "signup",
    components: {},
    data() {
        return {
            form: {
                name: "",
                email: "",
                password: "",
            },
        }
    },
    methods: {
        ...mapActions("auth", ["signup"]),
        submit() {
            this.signup(this.form)
                .then(() => {
                    this.$router.replace("dashboard")
                })
                .catch(() => {
                    console.log("faild")
                })
        },
    },
}
</script>
