<template>
    <b-container>
        <b-row>
            <b-col>
                <h1>Product</h1>
                <b-button class="float-right" variant="danger" @click="logout"
                    >Logout</b-button
                >
            </b-col>
        </b-row>
        <b-row>
            <b-col cols="3" v-for="(product, i) in products" :key="i">
                <b-card :footer="`Price ${product.price}`" class="mb-2">
                    <template #header>
                        {{ product.title }}
                        <b-button
                            variant="danger"
                            size="sm"
                            class="float-right"
                            @click="deleteClick(product.id)"
                        >
                            D
                        </b-button>
                        <b-button
                            variant="warning"
                            size="sm"
                            class="float-right mx-2"
                            @click="editClick(product)"
                        >
                            E
                        </b-button>
                    </template>
                    <b-card-title :title="product.title"></b-card-title>
                    <b-card-text>
                        <b-row>
                            <b-col>
                                <b-img
                                    :src="
                                        `https://products-backend-api.herokuapp.com/${product.image}`
                                    "
                                    fluid
                                    alt="Responsive image"
                                ></b-img>
                            </b-col>
                            <b-col>
                                {{ product.description }}
                            </b-col>
                        </b-row>
                    </b-card-text>
                </b-card>
            </b-col>
        </b-row>
        <!-- Add button -->
        <div class="d-flex  float-right">
            <b-button
                pill
                variant="primary"
                class="add-button"
                @click="addButtonClick"
                >Add new</b-button
            >
        </div>
        <!-- modal -->
        <b-modal
            id="modal-prevent-closing"
            ref="modal"
            :title="saveModal ? `Create Product` : `Edit product`"
            @show="resetModal"
            @hidden="resetModal"
            @ok="handleOk"
        >
            <form ref="form" @submit.stop.prevent="handleSubmit">
                <b-form-group
                    :state="nameState"
                    label-for="name-input"
                    invalid-feedback="Name is required"
                >
                    <b-form-input
                        id="name-input"
                        v-model="form.title"
                        required
                        placeholder="Title"
                    ></b-form-input>
                </b-form-group>
                <b-form-group
                    :state="nameState"
                    label-for="name-input"
                    invalid-feedback="Name is required"
                >
                    <b-form-textarea
                        id="name-input"
                        v-model="form.description"
                        :state="nameState"
                        required
                        placeholder="Description"
                    ></b-form-textarea>
                </b-form-group>
                <b-row>
                    <b-col>
                        <b-form-file
                            class="my-3"
                            plain
                            @change="getImage"
                        ></b-form-file>
                    </b-col>
                    <b-col v-if="!saveModal">
                        <b-img
                            :src="
                                `https://products-backend-api.herokuapp.com/${form.image}`
                            "
                            fluid
                            alt="Responsive image"
                        ></b-img>
                    </b-col>
                </b-row>
                <b-form-group
                    :state="nameState"
                    label-for="name-input"
                    invalid-feedback="Name is required"
                >
                    <b-form-input
                        id="name-input"
                        v-model="form.price"
                        :state="nameState"
                        type="number"
                        required
                        placeholder="Price"
                    ></b-form-input>
                </b-form-group>
            </form>
        </b-modal>
    </b-container>
</template>

<script>
import { mapActions, mapGetters } from "vuex"
import axios from "axios"

export default {
    name: "dashboard",
    components: {},
    data() {
        return {
            name: "",
            nameState: "null",
            submittedNames: [],
            form: {
                id: "",
                title: "",
                description: "",
                price: "",
                image: null,
            },
            saveModal: true,
        }
    },
    methods: {
        ...mapActions("product", [
            "getProducts",
            "addProduct",
            "deleteProduct",
            "editProduct",
        ]),
        ...mapActions("auth", ["signOut"]),
        checkFormValidity() {
            const valid = this.$refs.form.checkValidity()
            this.nameState = valid
            return valid
        },
        resetModal() {
            this.name = ""
            this.nameState = null
        },
        handleOk(bvModalEvt) {
            // Prevent modal from closing
            bvModalEvt.preventDefault()
            // Trigger submit handler
            this.handleSubmit()
        },
        handleSubmit() {
            //if yeas then save request else update request
            if (this.saveModal) {
                this.addProduct(this.form).then(() => {
                    this.$bvModal.hide("modal-prevent-closing")
                    this.form.title = ""
                    this.form.description = ""
                    this.form.price = ""
                })
            } else {
                this.editProduct(this.form).then(() => {
                    this.$bvModal.hide("modal-prevent-closing")
                    this.form.title = ""
                    this.form.description = ""
                    this.form.price = ""
                })
            }
        },
        deleteClick(item) {
            this.deleteProduct(item)
        },
        editClick(item) {
            this.saveModal = false
            this.form.id = item.id
            this.form.title = item.title
            this.form.description = item.description
            this.form.price = item.price
            this.form.image = item.image
            this.$bvModal.show("modal-prevent-closing")
        },
        addButtonClick() {
            this.form.title = ""
            this.form.description = ""
            this.form.price = ""
            this.$bvModal.show("modal-prevent-closing")
            this.saveModal = true
        },
        getImage(e) {
            let file = e.target.files[0]
            //console.log(file.size)
            if (file.size <= 10111775) {
                let reader = new FileReader()
                reader.onloadend = () => {
                    //console.log("RESULT", reader.result)
                    this.form.image = reader.result
                }
                reader.readAsDataURL(file)
            } else {
                alert("file is too larze")
            }
        },
        logout() {
            this.signOut()
                .then(() => {
                    this.$router.replace("/signin")
                })
                .catch(() => {
                    this.$router.replace("/signin")
                })
        },
    },
    mounted() {
        this.getProducts()
    },
    computed: {
        ...mapGetters({
            products: "product/get_products",
        }),
    },
}
</script>

<style scoped>
.add-button {
    position: fixed;
    bottom: 20px;
    right: 20px;
}
</style>
